body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Styling file for the client form */
.client-form-container {
  margin: 10px;
}

.client-submission {
  background-color: #ffffff; /* Light background color */
  max-width: 400px; /* Make the form narrower */
  padding: 15px; /* Reduce padding */
  border: 1px solid #ddd; /* Add a subtle border */
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1); /* Add a soft shadow */
  display: flex;
  flex-direction: column;
}

/* Title Styles */
.title-post-form {
  color: #008000;
  font-size: 20px; /* Slightly smaller title font size */
  margin-bottom: 10px; /* Reduce space between title and form */
}

/* Label Styles */
label {
  color: #007FFF;
  text-align: left;
  font-size: 14px; /* Smaller font size */
  margin-bottom: 5px; /* Add space between labels and inputs */
}

/* Input Styles */
.input-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5px; /* Reduce spacing between input containers */
}

.service-dates {
  display: flex;
  padding-top: 10px;
}
input[type="text"],
input[type="date"],
textarea {
  flex: 1 1; /* Make inputs flexible to share available space */
  padding: 5px; /* Reduce padding */
  border: 1px solid #ddd; /* Add borders to inputs */
  border-radius: 3px;
  font-size: 12px; /* Smaller font size */
  height: 24px; /* Reduce input height */
  margin-right: 5px; /* Add space between inputs */
}

/* Submit Button Styles */
.new-client-submit {
  margin-top: 10px;
  font-size: 14px; /* Smaller font size */
  padding: 5px 10px; /* Reduce padding */
  background-color: #008000;
  color: #ffffff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;

  /* Center text horizontally */
  text-align: center;
}

.new-client-submit:hover {
  background-color: #006400; /* Darken button color on hover */
}

@media screen and (max-width: 768px) {
  .client-form-container {
    margin: 0; /* Remove margin to utilize full screen width */
    padding: 0;
  }

  .client-submission {
    max-width: 100%; /* Allow form to take full screen width */
    padding: 10px;
    border: none; /* Remove border on smaller screens */
  }

  /* Adjust the title font size for smaller screens */
  .title-post-form {
    font-size: 18px;
  }

  /* Adjust label font size for smaller screens */
  label {
    font-size: 14px;
  }

  /* Adjust input font size for smaller screens */
  input[type="text"],
  input[type="date"],
  textarea {
    font-size: 12px;
  }

  /* Adjust input and textarea padding for smaller screens */
  input[type="text"],
  input[type="date"],
  textarea {
    padding: 8px;
  }

  /* Adjust button padding for smaller screens */
  .new-client-submit {
    padding: 8px 16px;
  }
}
:root {
  --form-radius: 8px;
  --form-background: #fff;
  --form-font-style: 'DM Sans', sans-serif;
  --form-text-size: 18px;
  --form-label-size: 16px;
}

.form-field {
  margin-bottom: 20px;
}

.form {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.form > section {
  flex-direction: column;
  justify-content: center;
  width: 100%;
}

.form > label {
  margin-top: 10px;
}

input[type="text"] {
  width: 95%;
}

textarea[type="text"] {
  width: 95%;
}
/* Styles for the update button */
.client-update-btn {
  background-color: #4CAF50; /* Green color for update button */
  color: white; /* Text color */
  border: none; /* Remove borders */
  border-radius: 5px; /* Rounded corners */
  padding: 10px 20px; /* Padding for the button */
  cursor: pointer; /* Cursor style on hover */
  font-size: 16px; /* Font size */
  margin-top: 10px; /* Add some top margin */
  transition: background-color 0.3s; /* Smooth background color transition */
  width: 120px; /* Set a fixed width */
}

/* Hover Effect */
.client-update-btn:hover {
  background-color: #45a049; /* Darker green color on hover */
  /* You can add more styles for the hover effect, like changing text color */
}

/* Active/Focus Effect (optional) */
.client-update-btn:active,
.client-update-btn:focus {
  outline: none; /* Remove default focus outline */
  /* You can add additional styles for the active/focus state if needed */
}


.form > input,
.form > textarea {
  font-size: 18px;
  font-size: var(--form-text-size);
  padding: 10px; /* Increase padding for input elements */
}

.form > label {
  font-size: 16px;
  font-size: var(--form-label-size);
  color: #000000;
}

section {
  display: flex;
  flex-direction: column;
}

