h2 {
  text-align: center;
  font-style: italic;
}
.modal {
  /* display: none; */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  justify-content: center;
  align-items: center;
  z-index: 1; 
}

/* Styles for the open modal */
.modal.open {
  display: flex;
}

/* Styles for the modal content */
.modal-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
  position: relative;
  text-align: left;
  width: 75%;
  margin: 0 auto;
}

/* Styles for the close button */
.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: #f44336;
  color: white;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  padding: 8px;
  font-size: 16px;
  width: 37px;
  height: 37px;
}

.close-button:hover {
  background-color: #d32f2f; /* Darker red color on hover */
}

/* Styles for the update button */
.update-btn {
  background-color: #4CAF50; /* Green color for update button */
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  padding: 10px 20px;
  margin-top: 10px;
  font-size: 16px;
}

.update-btn:hover {
  background-color: #45a049; /* Darker green color on hover */
}

.price {
  color: #008000;
  font-size: 30px;
}

@media screen and (max-width: 768px) {
  /* Modal styles */
  .modal.open {
    display: flex;
    flex-direction: column;
  }
  .modal-content {
    width: 90%; /* Adjust width for smaller screens */
    padding: 10px; /* Adjust padding */
    overflow-y: auto;
  }

  /* Close button styles */
  .close-button {
    top: 5px;
    right: 5px;
    width: 30px;
    height: 30px;
    padding: 5px;
    font-size: 14px;
  }

  /* Update button styles */
  .update-btn {
    font-size: 14px;
    padding: 8px 16px;
    margin-top: 5px;
  }

  .price {
    font-size: 20px; /* Adjust font size */
  }
}

.client-amount {
  background-color: #f0f0f0; /* Construction color background */
  padding: 20px; /* Add padding for spacing */
  border-radius: 5px; /* Rounded corners */
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1); /* Add a subtle shadow */
  margin-top: 20px; /* Add margin to separate from other content */
}

.client-amount p {
  font-size: 20px; /* Increase the font size */
  font-weight: 700; /* Make it bold */
  color: #008000; /* Green color */
}



/* Table Styles */
table {
  background: #f2f2f2;
  width: 100%;
  border-collapse: collapse;
  text-align: left;
  font-size: 16px; /* Keep the font size the same */
}

table th {
  background: #808080;
  padding: 8px; /* Slightly reduce padding */
  text-align: left;
  font-weight: bold;
  color: #ffffff;
}

table tr {
  border-bottom: 1px solid #ccc;
}

/* Table cell (td) styles */
table td {
  padding: 8px; /* Slightly reduce padding */
}

/* Alternate row background color for better readability */
table tr:nth-child(even) {
  background-color: #f9f9f9;
}

/* Hover effect on rows */
table tr:hover {
  background-color: #e2e2e2;
}

/* Truncate long text in table cells */
td > div {
  max-width: 200px; /* Reduce max width to truncate text */
  white-space: nowrap; /* Prevent text from wrapping */
  overflow: hidden;
  text-overflow: ellipsis; /* Add ellipsis (...) for overflowed text */
}

/* Basic Delete Button Styles */
.remove-client-submit {
  background-color: #f44336; /* Red background color */
  color: white; /* Text color */
  border: none; /* Remove borders */
  border-radius: 5px; /* Rounded corners */
  padding: 6px 12px; /* Smaller padding for the button */
  cursor: pointer; /* Cursor style on hover */
}

/* Hover Effect */
.remove-client-submit:hover {
  background-color: #d32f2f; /* Darker red color on hover */
  /* You can add more styles for the hover effect, like changing text color */
}

/* Active/Focus Effect (optional) */
.remove-client-submit:active,
.remove-client-submit:focus {
  outline: none; /* Remove default focus outline */
  /* You can add additional styles for the active/focus state if needed */
}

/* Edit Button Styles */
.edit-client-submit {
  width: 70px;
  padding: 6px 12px; /* Smaller padding for the button */
  font-size: 16px; /* Font size */
  background: #007FFF;
  color: #fff;
}

.quote-total {
  color: #008000;
  font-weight: 900;
}

@media screen and (max-width: 768px) {
  /* Adjust client-amount styles for mobile */
  .client-amount {
    padding: 10px;
    margin-top: 10px;
  }

  .client-amount p {
    font-size: 16px;
    font-weight: 600;
  }

  /* Adjust table container styles for mobile */
  .table-container {
    overflow-x: auto; /* Enable horizontal scrolling */
    max-width: 100%; /* Make the table container full width */
  }

  /* Adjust table styles for mobile */
  table {
    width: 100%; /* Make the table full width */
    font-size: 12px;
  }

  table th,
  table td {
    padding: 4px;
  }

  /* Adjust button styles for mobile */
  .remove-client-submit {
    padding: 3px 6px;
    font-size: 12px;
  }
}
